function ChangeThumbWidth(value) {

  let vClass;

  switch (value) {
    case 'small':
      vClass = 'small';
      break;
    case 'medium':
      vClass = 'medium';
      break;
    default:
      vClass = 'medium';
  }

  window.localStorage.setItem("sevengalaxies@thumbWidth", vClass);
  try {

    var items = document.querySelectorAll('.deckBuilder-card-thumb');

    items.forEach(function (item) {
      item.classList.remove('medium', 'small');
      item.classList.add(vClass);
    });
  }

  catch (error) { }

  // setBackground(localTheme + background_new);

  // }
  // return (
  //   <div className="background-control" onClick={() => handleBackground(props.value)}></div>
  // )
}
export default ChangeThumbWidth;